import React from "react";
import {useTags} from "../hooks/useTags";
import {useNavigate, useParams} from "react-router-dom";
import Layout from "../components/Layout";
import Icon from "../components/icon";
import {Button} from "../components/Button";
import styled from "styled-components";
import {Input} from "../components/Input";
type Params = {
    id:string
}
const Topbar = styled.header`
  display: flex;
  justify-content: space-between;
  line-height: 20px;
  padding: 14px;
  background: white;
`
const InputWrapper = styled.div`
  background: white;
  padding: 0 16px;
  margin-top: 8px;
`
const Tag:React.FC = ()=>{
    const {findTag,updateTags,deleteTag} = useTags()
    let {id} = useParams<Params>()

    const tag:any = findTag(parseInt(id as string))
    const history = useNavigate()
    const onClickBack = ()=>{
        history('/tags')
    }
    const tagContent = (tag:{id:number,name:string})=>{
        return (<div>
            <InputWrapper>
                <Input label='标签名' type='text' placeholder='请输入你想填写的标签名' value={tag.name}
                       onChange={(e)=>updateTags(tag.id,{name:e.target.value})}
                />
            </InputWrapper>
            <Button onClick={()=>deleteTag(tag.id)}>删除标签</Button>
        </div>)
}
    return(
        <Layout>
        <Topbar>
        <Icon name='left' onClick={onClickBack} />
        <span>编辑标签</span>
        <Icon />
        </Topbar>
            {tag ? tagContent(tag) : <div>tag不存在</div>}
        </Layout>
    )
}
export {Tag}
