import Layout from "../components/Layout";
import React, {useState} from "react";
import {TagsSection} from "./Account/TagsSection";
import {CategorySection} from "./Account/CategorySection";
import {NoteSection} from "./Account/NoteSection";
import {NumberPadSection} from "./Account/NumberPadSection";
import styled from "styled-components";

import {useRecords} from "../hooks/useRecords";
const MyLayout = styled(Layout)`
  display:flex;
  flex-direction: column;
`
const CategoryWrapper = styled.div`
  background: #c4c4c4;
`
type Category = '-' | '+'
const defaultFormData = {
    tagIds:[] as number[],
        note:'',
    category:'-' as Category,
    amount:0
}
function Account() {
    const [selected,setSelected] = useState(defaultFormData)
    const onchange = (obj:Partial<typeof selected>)=>{
        setSelected({...selected,...obj})
    }
    const {addRecord} = useRecords()
    const submit = ()=>{
        if(addRecord(selected)){
            alert('保存成功')
            setSelected(defaultFormData)
        }

    }
    return (
        <MyLayout scrollTop={9999}>
            <TagsSection value={selected.tagIds}  onChange={(tagIds)=>onchange({tagIds})}/>
            <NoteSection value={selected.note} onChange={(note)=>onchange({note})} />
            <CategoryWrapper><CategorySection value={selected.category} onChange={(category)=>onchange({category})}/></CategoryWrapper>
            <NumberPadSection value={selected.amount} onChange={(amount)=>onchange({amount})}
                onOk={submit}
            />
        </MyLayout>
    );
}
export default Account