import styled from "styled-components";
import {NavLink} from "react-router-dom";
import React from "react";
import Icon from "./icon";

const NavWrapper = styled.nav`
  box-shadow: 0 0 3px rgba(0,0,0,0.5);
  line-height: 24px;
  > ul{
    display: flex;
    >li{
      width: 33.3333%;
      text-align: center;
      a{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 4px 0;
        .icon {
          width: 24px;
          height: 24px;
        }
        &.selected{
          color: #f60;
          .icon{
            fill: #f60;
          }
        }
      }
      
    }
  }
`;
const Nav = ()=>{
    return (
        <NavWrapper>
            <ul>
                <li>
                    <NavLink className={({isActive})=>isActive?'selected':''} to="/tags"><Icon name='tag' />标签页</NavLink>
                </li>
                <li>

                    <NavLink className={({isActive})=>isActive?'selected':''} to="/account"><Icon name='money' />记账页</NavLink>
                </li>
                <li>

                    <NavLink className={({isActive})=>isActive?'selected':''} to="/statistics"><Icon name='chart' />统计页</NavLink>
                </li>
            </ul>
        </NavWrapper>
    )
}
export default Nav